
export default {
  doFormPostDatas(url, datas, cb, errorCb) {
    fetch(url, {
        method: 'POST',
        headers: {
            "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
        },
        credentials: 'same-origin',
        body: datas
    }).then(function(response) {
        return response.json()
    }).then(function(json) {
        cb(json)
    }).catch(function(ex) {
        errorCb(ex)
    })
  }
}